* {
  box-sizing: border-box;
}
.spc-product {
  font-family: Helvetica, sans-serif;
}
.spc-product-disabled,
[disabled] {
  pointer-events: none;
  cursor: not-allowed;
  opacity: 0.8;
}
/* Backgrounds */
.spc-product-bg-success-faded {
  background-color: #d1f5e0;
  color: #2ecc71;
}
.spc-product-bg-contrast-faded {
  background-color: #fbd8e4;
  color: #E91E63;
}
.spc-product-bg-warning-faded {
  background-color: #fee8c9;
  color: #f69305;
}
.spc-product-bg-primary-faded {
  background-color: #cbe7fd;
  color: #0b8bf1;
}
.spc-product-bg-default-faded {
  background-color: #aaaaaa;
  color: #444444;
}
/* --- */
/* Hovers */
.spc-product-hover-contrast:hover,
.spc-product-hover-contrast.spc-product-active {
  color: #E91E63;
  border-color: rgba(233, 30, 99, 0.2);
}
.spc-product-hover-primary:hover,
.spc-product-hover-primary.spc-product-active {
  color: #0b8bf1;
  border-color: rgba(11, 139, 241, 0.2);
}
.spc-product-hover-success:hover,
.spc-product-hover-success.spc-product-active {
  color: #2ecc71;
  border-color: rgba(46, 204, 113, 0.2);
}
.spc-product-hover-warning:hover,
.spc-product-hover-warning.spc-product-active {
  color: #f69305;
  border-color: rgba(246, 147, 5, 0.2);
}
.spc-product-hover-default:hover,
.spc-product-hover-default.spc-product-active {
  color: #444444;
  border-color: rgba(68, 68, 68, 0.2);
}
/* --- */
.spc-product {
  width: 100%;
  height: 390px;
  max-width: 250px;
  position: relative;
  z-index: 1;
}
.spc-product-side-actions {
  position: absolute;
  z-index: 3;
  top: 10px;
  right: 10px;
}
.spc-product-side-action:not(.spc-product-active) {
  transform: scale(0);
}
.spc-product-content {
  background-color: #ffffff;
  padding: 15px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  border-radius: 12px;
  transition: border-color 0.2s ease-in-out;
  border: 1px solid #eee;
}
@media (min-width: 768px) {
  .spc-product-content {
    transition: box-shadow 0.2s ease-in-out;
  }
  .spc-product-content:hover {
    box-shadow: rgba(100, 100, 111, 0.15) 0px 7px 29px 0px;
  }
}
.spc-product-top {
  margin-bottom: 10px;
  color: #7e7e7e;
  font-size: 11px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.spc-product-top .spc-product-category {
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
}
.spc-product-top .spc-product-by {
  margin-left: 5px;
  display: flex;
  width: 45%;
  justify-content: flex-end;
}
.spc-product-top .spc-product-by-link {
  margin-left: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
}
.spc-product-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 15px;
  max-height: 37px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  max-width: 100%;
  width: fit-content;
}
.spc-product-buttons {
  display: none;
  margin-top: 20px;
}
.spc-product-buttons .spc-product-button {
  margin-right: 5px;
  margin-bottom: 10px;
  vertical-align: middle;
  display: inline-flex;
}
.spc-product-buttons .spc-product-quantity {
  vertical-align: middle;
  display: inline-flex;
  margin-bottom: 10px;
}
.spc-product:hover {
  z-index: 2;
}
.spc-product:hover .spc-product-content {
  border-color: rgba(11, 139, 241, 0.25);
}
.spc-product:hover:not(.spc-product-info-only) .spc-product-content {
  padding-bottom: 5px;
}
.spc-product:hover .spc-product-buttons {
  display: block;
  white-space: normal;
}
.spc-product:hover .spc-product-title {
  white-space: normal;
}
.spc-product:hover .spc-product-open-zoom {
  transform: scale(1);
}
.spc-product:hover .spc-product-side-action {
  transform: scale(1);
}
.spc-product:hover .spc-product-image {
  transform: scale(1.05);
}
.spc-product:hover .spc-product-tags {
  white-space: normal;
  margin-bottom: 10px;
}
.spc-product:hover .spc-product-tag {
  margin-bottom: 5px;
}
.spc-product:hover.spc-product-info-only .spc-product-tags {
  margin-bottom: 0px;
}
.spc-product:hover.spc-product-info-only .spc-product-tag {
  margin-bottom: 0px;
}
@media (max-width: 768px) {
  .spc-product {
    z-index: 2;
  }
  .spc-product .spc-product-content {
    border-color: rgba(11, 139, 241, 0.25);
  }
  .spc-product:not(.spc-product-info-only) .spc-product-content {
    padding-bottom: 5px;
  }
  .spc-product .spc-product-buttons {
    display: block;
    white-space: normal;
  }
  .spc-product .spc-product-title {
    white-space: normal;
  }
  .spc-product .spc-product-open-zoom {
    transform: scale(1);
  }
  .spc-product .spc-product-side-action {
    transform: scale(1);
  }
  .spc-product .spc-product-image {
    transform: scale(1.05);
  }
  .spc-product .spc-product-tags {
    white-space: normal;
    margin-bottom: 10px;
  }
  .spc-product .spc-product-tag {
    margin-bottom: 5px;
  }
  .spc-product.spc-product-info-only .spc-product-tags {
    margin-bottom: 0px;
  }
  .spc-product.spc-product-info-only .spc-product-tag {
    margin-bottom: 0px;
  }
  .spc-product {
    height: auto;
  }
  .spc-product-content {
    border: none;
    padding: 0px;
    position: static;
  }
  .spc-product-labels {
    top: 0;
    left: -5px;
  }
}
@media (min-width: 768px) {
  .spc-product.spc-product-inline {
    height: auto;
    width: auto;
    max-width: 500px;
  }
  .spc-product.spc-product-inline .spc-product-content {
    flex-direction: inherit;
    position: relative;
    padding-bottom: 5px;
  }
  .spc-product.spc-product-inline:hover .spc-product-by {
    margin-right: 35px;
    transition: margin-right 0.2s ease-in-out;
  }
  .spc-product.spc-product-inline .spc-product-buttons {
    display: block;
  }
  .spc-product.spc-product-inline .spc-product-gallery-wrapper {
    margin-bottom: 0px;
    padding-top: 10px;
    height: fit-content;
    min-width: 35%;
    max-width: 200px;
  }
  .spc-product.spc-product-inline .spc-product-details {
    padding-top: 10px;
    padding-bottom: 10px;
    flex: 1;
    display: block;
    overflow: hidden;
  }
  .spc-product.spc-product-inline .spc-product-tags {
    white-space: normal;
    margin-bottom: 10px;
  }
  .spc-product.spc-product-inline .spc-product-tag {
    margin-bottom: 5px;
  }
}
