.anchor(@color, @hoverColor) {
    cursor: pointer;
    transition: color 0.2s ease-in-out;
    text-decoration: none !important;
    display: inline-flex;
    align-items: center;

    &,
    &:link,
    &:visited,
    &:active,
    &:focus {
        color: @color;
    }

    @media (min-width: @md) {
        &:hover {
            color: @hoverColor;
        }
    }

    .@{classPrefix}-icon {
        margin-right: 10px;
    }
}

.@{classPrefix}-anchor-primary {
    .anchor(@primary, lighten(@primary, 10%));
}

.@{classPrefix}-anchor-success {
    .anchor(@success, lighten(@success, 10%));
}

.@{classPrefix}-anchor-warning {
    .anchor(@warning, lighten(@warning, 10%));
}

.@{classPrefix}-anchor-contrast {
    .anchor(@contrast, lighten(@contrast, 10%));
}

.@{classPrefix}-anchor-default {
    .anchor(@default, @primary);
}
@classPrefix: spc-product;@default: #444444;@primary: #0b8bf1;@contrast: #E91E63;@warning: #f69305;@success: #2ecc71;@borderRadius: 12px;@sm: 576px;@md: 768px;@lg: 992px;@xl: 1200px;@xxl: 1400px;@extraSmall: (max-width: 576px);@small: (max-width: 768px);@medium: (max-width: 992px);@large: (max-width: 1200px);@extraLarge: (max-width: 1400px);@largest: (min-width: 1200px);